.App {
  width: 1100px;
  padding: 0 20px;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.player__row {
  display: flex;
  align-items: center;
}

.game__wrapper {
  display: inline-grid;
  grid-template-columns: 150px 150px 150px;
  margin-top: 50px;

}

.game__box {
  height: 150px;
  width: 150px;
  text-align: center;
  font-size: 120px;
  line-height: 110px;
}

.game__box:not(:nth-child(3n)) {
  border-right: 2px solid;
}

.game__box:not(:nth-child(7), :nth-child(8), :nth-child(9)) {
  border-bottom: 2px solid;
}

.player__info {
  margin-right: 60px;
}

.player {
  font-weight: 600;
  font-size: 18px;
}

.game {
  display: flex;
  justify-content: space-between;
}

.chat {
  background-color: rgb(232, 232, 232);
  padding: 20px;
  width: 400px;
  height: 360px;
  overflow: auto;
  border-radius: 5px;
}
.chat__wrapper h2 {
  text-align: center;
}

.chat__from {
  font-size: 17px;
  font-weight: 600;
  margin-bottom: 0;
}

.chat__text {
  margin-top: 1px;
}

.chat__send {
  width: 100%;
  margin-top: 25px;
  height: 25px;
}

.chat__send input {
  width: calc(100% - 87px);
  height: 100%;
}

.chat__send button {
  height: calc(100% + 5px);
}